<template>
<div class="markdown-editor">
  <textarea v-model=markdown
             name="markdown"
             v-on:click=onClick
             v-on:keyup=onKeyUp
             @keydown.ctrl.66=onBold
             @keydown.ctrl.73=onItalic></textarea>
  <div class="result-area" v-html="html">{{html}}
  </div>
</div>
</template>

<script>
const $ = require('jquery')
window.$ = $

const Vue = require('vue')
const MarkdownIt = require('markdown-it')

const md = new MarkdownIt({
  html: true,
  linkify: true,
  typographer: true,
  highlight: true
})

const storage = window.localStorage
let markdown = storage.getItem('markdown')

if (typeof markdown !== 'string') {
  markdown = ''
}

const MarkdownEditor = new Vue({
  name: 'MarkdownEditor',
  data: function () {
    return {
      markdown: markdown,
      html: md.render(markdown)
    }
  },
  watch: {
    markdown: function (val) {
      this.html = md.render(val)
      storage.setItem('markdown', val)
    }
  },
  methods: {
    onBold: function (event) {
      event.preventDefault()

      const $textarea = $("textarea[name='markdown']")

      const cursorPosition = $textarea.prop("selectionStart")
      const selectionEnd = $textarea.prop("selectionEnd")

      let text = $textarea.val()
      let selectedText = text.substring( cursorPosition, selectionEnd )
      let formattedText = `__${selectedText}__`

      text = text.substring(0, cursorPosition) + formattedText + text.substring( selectionEnd, text.length)
      $textarea.val( text )
      $textarea[0].setSelectionRange( cursorPosition, selectionEnd + 4 )
    },
    onItalic: function(event) {

    },
    onClick: function () {
      this._scrollRightPanelCursorPosition()
    },
    onKeyUp: function (event) {      
      this._scrollRightPanelCursorPosition()
      //console.log( event.ctrlKey )
      //console.log( event )
    },
    _formatSelection( format ) {
      const $textarea = $("textarea[name='markdown']")

      const cursorPosition = $textarea.prop("selectionStart")
      const selectionEnd = $textarea.prop("selectionEnd")

      let text = $textarea.val()
      let selectedText = text.substring( cursorPosition, selectionEnd )
      let formattedText = `__${selectedText}__`

      text = text.substring(0, cursorPosition) + formattedText + text.substring( selectionEnd, text.length)
      $textarea.val( text )
      $textarea[0].setSelectionRange( cursorPosition, selectionEnd + 4 )      
    },
    _scrollRightPanelCursorPosition() {
      //Find text area element 
      const $textarea = $("textarea[name='markdown']")
      
      //Get markdown text
      const text = $textarea.val()

      //Editor cursor position
      const cursorPosition = $textarea.prop("selectionStart")

      //Calculate percentual position of the cursor, value can be from 0 - 1
      const position = cursorPosition / text.length
      const $resultArea = $('.result-area')
      const scrollHeight = $resultArea.prop("scrollHeight")

      //Scroll position view area to right position
      $resultArea.scrollTop(scrollHeight * position)
    }
  }
})

module.exports = MarkdownEditor
</script>

